@font-face {
  font-family: 'GTWalsheimPro';
  font-style: normal;
  font-weight: 400;
  font-display: fallback;
  src: local('GTWalsheimPro Regular'), local('GTWalsheimPro-Regular'),
        url('../fonts/GTWalsheimPro-Regular.woff2') format('woff2'), /* Super Modern Browsers */
        url('../fonts/GTWalsheimPro-Regular.woff') format('woff'), /* Modern Browsers */
}

@font-face {
  font-family: 'GTWalsheimPro';
  font-style: normal;
  font-weight: 500;
  font-display: fallback;
  src: local('BloggerSans Medium'), local('GTWalsheimPro-Medium'),
        url('../fonts/GTWalsheimPro-Medium.woff2') format('woff2'), /* Super Modern Browsers */
        url('../fonts/GTWalsheimPro-Medium.woff') format('woff'), /* Modern Browsers */
}


@font-face {
  font-family: 'GTWalsheimPro';
  font-style: normal;
  font-weight: 700;
  font-display: fallback;
  src: local('GTWalsheimPro Bold'), local('GTWalsheimPro-Bold'),
        url('../fonts/GTWalsheimPro-Bold.woff2') format('woff2'), /* Super Modern Browsers */
        url('../fonts/GTWalsheimPro-Bold.woff') format('woff'), /* Modern Browsers */
}

